import React, { useEffect, useState } from "react";
import img from "../Assests/image.jpeg";
import pic from "../Assests/logo.jpeg";
import LanguageIcon from "@mui/icons-material/Language";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import validation from "./Validation";

function Login() {
  const [values, setValues] = useState('');
  const [errors, setErrors] = useState({});

  function handleChange(e) {
    setValues({ ...values, [e.target.email]: e.target.value });
  }
  function handleSubmit(e) {
    e.preventDefault();
    setErrors(validation(values));
  }
  useEffect(() => {
    if (
      Object.keys(errors).length === 0 &&
      values.email !== "" &&
      values.password !== ""
    ) {
    //   alert("Form Submitted");
    }
  }, [errors]);

  return (
    <div className="bg-black">
      <div className="min-h-screen p-20">
        <div className="container mx-auto">
          <div className="flex flex-col columns-2 lg:flex-row  bg-no-repeat bg-white mx-auto p-5">
            <div
              className=" lg:w-1/2 justify-center bg-no-repeat bg-cover bg-center"
              style={{ backgroundImage: `url(${img})`  ,'borderRadius':'30px' }}
            >
              <h1
                className="text-white pt-64 px-20 bg-no-repeat text-5xl font-bold"
                style={{ backgroundImage: `url(${pic})` ,'borderRadius':'30px' }}
              >
                100% Uptime😎
              </h1>
              <div>
                <p className="flex text-zinc-400 pt-6 px-20 text-4xl ">
                  Zero Infrastructure Management
                </p>
              </div>
              <div className="text-zinc-400 pt-52 px-2">
                <h1>
                  <LanguageIcon />
                  aesthisia.com  <span className="pl-96 text-white">  <LinkedInIcon />
                  <FacebookIcon />
                  <InstagramIcon /></span>
                </h1>
                
              </div>
            </div>
            <div className="w-96 lg:w-1/2 py-16 px-12 text-center bg-no-repeat bg-size-30">
              <h1 className="text-4xl pt-4 pb-9 pl-9 text-black align-middle text-center font-poppins">
                Welcome
                <span className="text-teal-600 text-4xl flex-wrap">Back!</span>
              </h1>
              <h5 className="text-zinc-400 p-0">Glad to see you Again!</h5>

              {/* form start */}
              <form onSubmit={handleSubmit}>
                <div className="mt-5">
                  <input
                    placeholder="Enter your Email"
                    value={values.email}
                    name="email"
                    onChange={handleChange}
                    className="border w-96 border-gray-400 py-4 px-4 rounded-lg"
                  />
                  {errors.email && (
                    <p style={{ color: "red", fontSize: "13px" }}>
                      {errors.email}
                    </p>
                  )}
                </div>
                <div className="mt-5">
                  
                    
                    <input
                      type="password"
                      placeholder="Enter your Password"
                      value={values.password}
                      onChange={handleChange}
                      className="border w-96 border-gray-400 py-4 px-4 rounded-lg"
                    />
                    {errors.password && (
                      <p style={{ color: "red", fontSize: "13px" }}>
                        {errors.password}
                      </p>
                    )}
                 
                </div>
                <p className="pr-0 pt-2 pl-56 text-sm flex-end text-zinc-400 ">
                  Forget your password?
                </p>
                <div className="mt-5">
                  <button
                    type="submit"
                    className="w-96 text-2xl bg-black  py-4 px-4 text-center text-white shadow-2xl rounded-md"
                  >
                    Login
                  </button>
                </div>
                <div className="text-black font-30 p-16">
                  Don't have an account yet?{" "}
                  <span className="text-teal-600 text-xl flex-wrap">
                    Sign Up
                  </span>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
